* {
box-sizing: border-box;
}
body {
	font-family: "Century Gothic", 'Lato', sans-serif;
	
}
 a {
	 text-decoration: none;
}
.grid {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	position: relative;
	background: rgba(239, 239, 231, 0.836);
	text-align: center;
	padding: 0 2em;
	min-height: 100vh;
	}


