.audio-jungle__img, .player {
	width: 40vw;
	padding: 1rem 1rem;
	}
	
	
@media screen and (max-width: 800px) {
	.grid {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		min-height: 100vh;
		/*TODO: zmiana żeby nie zakrywało playerów */
		position: relative;
		background: rgba(239, 239, 231, 0.836);
		text-align: center;
		
	}
	
	.audio-jungle__img, .player {
		min-width: 70vw;
		padding: 1rem 1rem;
	}

}


.logo {
	width: 40vw;
	
}

.AJ {
	width: 40vw;
}

.logo__nav {
	width: 70px;
}

