 .et-hero-tabs, .et-slide {
	 display: flex;
	 flex-direction: column;
	 justify-content: center;
	 align-items: center;
	 min-height: 100vh;
	 /*TODO: zmiana żeby nie zakrywało playerów */
	 position: relative;
	 background: rgba(239, 239, 231, 0.836);
	 text-align: center;
	 padding: 0 2em;
}


/* TODO: Odstęp między kontenerami, tło zasłania div */
/* .et-slide {
	padding: 180px 0;
} */
 .et-hero-tabs h1, .et-slide h1, .grid h1 {
	 font-size: 2rem;
	 margin: 0;
	 letter-spacing: 1rem;
	 padding: 2rem 0;
}
 .et-hero-tabs h3, .et-slide h3, .grid h3 {
	 font-size: 1rem;
	 letter-spacing: 0.3rem;
	 opacity: 0.6;
}
 .et-hero-tabs-container {
	 display: flex;
	 flex-direction: row;
	 position: absolute;
	 bottom: 0;
	 width: 100%;
	 height: 70px;
	 box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
	 background: #fff;
	 z-index: 10;
}
 .et-hero-tabs-container--top {
	 position: fixed;
	 top: 0;
}
 .et-hero-tab {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 flex: 1;
	 color: #000;
	 letter-spacing: 0.1rem;
	 transition: all 0.5s ease;
	 font-size: 0.8rem;
}
 .et-hero-tab:hover {
	 color: white;
	 background: rgb(118, 196, 182);
	 transition: all 0.5s ease;
}
 .et-hero-tab-slider {
	 position: absolute;
	 bottom: 0;
	 width: 0;
	 height: 3px;
	 background: rgb(238, 60, 72);
	 transition: left 0.3s ease;
}
 @media (min-width: 800px) {
	 .et-hero-tabs h1, .et-slide h1 {
		 font-size: 3rem;
	}
	 .et-hero-tabs h3, .et-slide h3 {
		 font-size: 1rem;
	}
	 .et-hero-tab {
		 font-size: 1rem;
	}
}